import Vue from 'vue'
import Vuex from 'vuex'
import {http} from '@/utils/http.js'
Vue.use(Vuex)

let lifeData = {};

try{
	// lifeDataAPP
	lifeData = uni.getStorageSync('lifeData');
}catch(e){
	
}

// APPstate
let saveStateKeys = ['vuex_user', 'vuex_token'];

// 
const saveLifeData = function(key, value){
	// 
	if(saveStateKeys.indexOf(key) != -1) {
		// lifeData
		let tmp = uni.getStorageSync('lifeData');
		// APPlifeData{}
		tmp = tmp ? tmp : {};
		tmp[key] = value;
		// lifeData
		uni.setStorageSync('lifeData', tmp);
	}
}
const store = new Vuex.Store({
    // state
	state: {
		showLoginPop:false,
		currentMsg: null,
		amountDetail:{}
	},
	mutations: {
		SetMessages(state, msg) {
			state.currentMsg = msg;
		},
        changeShowLoginPop(state,value){
            state.showLoginPop=value
        },
		$uStore(state, payload) {
			// stateuser.info.score = 1
			let nameArr = payload.name.split('.');
			let saveKey = '';
			let len = nameArr.length;
			if(len >= 2) {
				let obj = state[nameArr[0]];
				for(let i = 1; i < len - 1; i ++) {
					obj = obj[nameArr[i]];
				}
				obj[nameArr[len - 1]] = payload.value;
				saveKey = nameArr[0];
			} else {
				// state
				state[payload.name] = payload.value;
				saveKey = payload.name;
			}
			// 
			saveLifeData(saveKey, state[saveKey])
		},
		getAmountDetail(state, data){
			state.amountDetail=data
		}
	},
	actions:{
		amountDetail(state, payload){
			uni.showLoading({
				title:''
			})
			http({url:'/order/account/myAccount'}).then(res=>{
				uni.hideLoading()
				state.commit('getAmountDetail',res.data)
			})
		}
	}
})

export default store
